@import url('https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap');

*{
  margin: 0;
  padding: 0;
  font-family: 'Source Sans Pro', sans-serif;
}

nav div.main-nav-con{
  /* border: 1px solid black; */
}

div.com-name a{
  text-decoration: none;
  color: #2878EB !important;
}

nav div.main-nav-con div.nav-con{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 100px;
  /* border: 1px solid red; */
}

nav div.main-nav-con div.nav-con div.menu ul{
  display: flex;
  list-style: none;
}

nav div.main-nav-con div.nav-con div.menu ul a{
  text-decoration: none;
  color: black;
}

div.nav-con div.menu ul li{
  padding: 0px 10px;
  font-size: 1.2em;
}

div.nav-con div.menu ul li.bl{
  position: relative;
}

div.nav-con div.menu ul li.bl:hover  > div{
  display: block;
}

div.nav-con div.menu ul li.bl > div{
  position: absolute;
  z-index: 10;
  background-color: rgb(172, 172, 172);
  padding: 10px 5px;
  display: none;
}

div.nav-con div.menu ul li.bl > div :not(no){
  margin: 10px 0px;
}

div.nav-con div.menu button{
 display:none;
 padding: 0px 10px;
 font-size: 2em;
 border-radius: 0px;
 border: 1px solid rgb(233, 233, 233);
}

div.phone-nav ul{
  list-style: none;
}

div.phone-nav{
  /* border: 1px solid black; */
  overflow: hidden;
  height: 10px;
  transition: all 0.5s;
  display: none;
}

div.phone-nav ul a{
  text-decoration: none;
  color: black;
}

div.phone-nav ul li{
  padding: 10px 20px;
}

div.phone-nav ul li.pbl{
  position: relative;
}

div.phone-nav ul li.pbl:hover > div{
  display: block;
}

div.phone-nav ul li.pbl > div{
  position: absolute;
  /* top: -100%; */
  padding: 0px 5px;
  z-index: 10;
  display: none;
  background-color: rgb(172, 172, 172);
}

div.phone-nav ul li.pbl > div :not(no){
  margin: 5px 0px;
}

header{
  position: relative;
  /* border: 1px solid black; */
}

div.head-con{
  background: linear-gradient(rgba(255, 255, 255, 0.9), rgba(230, 232, 235, 0.9)), url("./images/head.jpg"), no-repeat center center;
  background-size: cover;
  padding: 80px 0px;

}

div.head-content{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 40px;
}

div.head-content h1{
  font-size: 3em;
  color: rgb(100, 101, 102);
  opacity: 0.8;
}

div.head-content h2{
  font-size: 2em;
  color: #2878EB !important;

}

div.head-content form{
  margin-top: 20px;
 /* border: 1px solid black; */
 width: 40%;
 display: flex;

}

div.head-content input{
  width: 100%;
  height: 30px;
  padding: 10px 20px;
  font-size: 1em;
  border: none;
  outline: none;

}

div.head-content form a{
  text-decoration: none;
  color: black;
}

div.head-content form div{
  background-color: tomato;
  padding: 0px 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

div.head-content > :not(no){
 padding: 10px 0px;
}

header img{
  /* border: 1px solid black; */
  position: absolute;
  bottom: 0px;
  width: 100%;
 
}

main{
  padding: 10px 0px;
}

main div.main-con{
  /* border: 1px solid gray; */
  padding: 10px;
}

main div.main-content-con{  
  /* border: 1px solid gold; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

main div.main-content-con > :not(not){  
 padding: 20px 0px;
}

main div.main-content-con div.main-text-con h2{  
  font-size: 3em;
}

 div.main-blog-navigation{ 
  /* border: 10px solid gold; */
  padding: 10px;
 display: flex;
 justify-content: center;
 align-items: center;
}

 div.main-blog-navigation > :not(no){  
  margin: 0px 10px;
}

 div.main-blog-navigation button{  
  border: none;
  outline: none;
  padding: 5px 10px;
  font-size: 1em;
}

 div.main-blog-navigation p{  
 font-weight: bold;
}

main div.main-blog-con{
  padding: 10px;
  width: 1100px;
  /* border: 1px solid green; */
  display: grid;
  grid-template-columns: auto auto auto;
  gap:30px;
}

main div.main-blog-con > div{
  position: relative;
  /* border: 1px solid black; */
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0px -100px 80px 10px inset;
  width: 100%;
  overflow: hidden;
  /* height: 400px; */
}

main div.main-blog-con > div h2{
  font-size: 1em;
}

main div.main-blog-con > div p{
  font-size: 1em;
}

main div.main-blog-con > div div{
  position: absolute;
  bottom: 0%;
  left: 0%;
  padding: 10px;
  color: white;
  background-color: rgba(1, 3, 7, 0.8);
  width: 100%;
 }

main div.main-blog-con > div img{
 opacity: 1;
 box-shadow: 10px 10px 10px 10px inset;
 width: 100%;
 transition: all 0.5s;
}

main div.main-blog-con > div:hover img{
  width: 110%;
}


footer div.footer-image {
  background: url("./images/overlay-top.png") top center no-repeat;
  background-size: cover;
  width: 100%;
  height: 50px;
  /* border: 1px solid gray; */
  background-color: #120F2D !important;

}

footer div.footer-content {
  padding: 20px;
  text-align: center;
  font-size: 1.5em;
  background-color: #120F2D !important;
  color: white;


}

div.about-main-con{
  /* border: 1px solid black; */
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 30px;
}

div.about-main-con h1{
  font-size: 2em;
  margin: 20px 0px;
}

div.about-main-con div.about-content-con{
  display: flex;
  /* flex-direction: column; */
  /* border: 1px solid firebrick; */
  width: 800px;
}

div.about-content-con div.about-img{
    /* border: 1px solid red; */
    display: flex;
    justify-content: center;
    align-items: center;
}

div.about-content-con div.about-img img{
  width: 65%;
  animation: manima 4s ease infinite;
  /* transition: all 2s; */
  transform: scale(1);
  transform-origin: center;
}

div.about-content-con div.about-text{
 display: flex;
 justify-content: center;
 flex-direction: column;
 align-items: center;
 padding: 20px;
 background-color: rgb(241, 238, 238);
}

div.about-content-con div.about-text p{
  text-align: justify;
  line-height: 1.4em;
  margin: 10px 0px;
 }


 div.main-contact-con{
  /* border: 1px solid gray; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px 0px;
  margin: 40px 0px;
 }

 div.main-contact-con h1{
  font-size: 2em;
  padding: 20px 0px;
 }

 div.contact-form-con{
  /* border: 1px solid red; */
  display: flex;
  flex-direction: column;
  width: 250px;
  background-color: #2878EB !important; 
  border-radius: 20px;
  padding-top:20px;
  padding-left:20px ;
}

div.contact-form-con form >input{
  font-size: 1em;
}

 div.contact-form-con form > :not(no){
    display: block;
    width: 80%;
    border: none;
    outline: none;
    background-color: white;
    margin: 10px 0px;
    padding: 5px 10px;
 }

 div.contact-form-con div.contact-cir{
  position: relative;
  height: 100px;
  /* border: 1px solid rebeccapurple; */
  overflow: hidden;
}

 div.contact-form-con div.contact-cir div:first-child{
  position: absolute;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: #fbaaaa;
  bottom: 20px;
  right: 20px;
}

div.contact-form-con div.contact-cir div:last-child{
  position: absolute;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background-color: rgb(255 49 125);
  bottom: -30px;
  right: -30px;
}


div.movies-main-blog-con{
  /* border: 1px solid black; */
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 20px 0px;
  margin: 20px 0px;
}

div.movies-main-blog-con h2{
  text-align: center;
  font-size: 2em;
  padding: 10px 0px;
  margin: 10px 0px;
}

div.movies-blog-content-con{
  width: 1100px;
  /* border: 3px solid red; */
  display: grid;
  grid-template-columns: 25% 25% 25% 25%;
  
  margin: 20px 0px;
  box-sizing: border-box;
}

div.movies-blog-content-con > :not(no){
 margin: 10px 5px;
}

div.movies-blog-content-con div.movies-blog-con{
  /* border: 2px solid yellow; */
  padding: 0px;
  background-color: #f5f5f5;
}

div.movies-blog-con div.movies-img-con{
  /* border: 2px solid green; */
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  background-color: rgb(248, 248, 248);
}

div.movies-blog-con div.movies-img-con:hover img{
 transform: scale(1.2);
}

div.movies-blog-con div.movies-img-con img{
  width: 100%;
  /* height: 200px; */
  /* object-fit: cover;*/
  transition: all 0.5s;

}
div.movies-blog-con > div:last-child {

 /* border: 1px solid hotpink; */
 padding: 0px 5px;
}


div.movies-blog-con > div:last-child :not(no){

  margin: 10px 0px;
}

div.movies-blog-con > div:last-child h4.movies-blog-type{
  color: rgb(240, 58, 231);
}

div.movies-blog-con > div:last-child h3{
  font-size: 1.1em;
}


@media screen and (max-width:1250px){

  main div.main-con{
    /* border: 1px solid gray; */
    padding: 30px;
  }

  main div.main-blog-con{
    width: 100%;
  }

  div.movies-blog-content-con{
   width: 100%;
  }
}



@media screen and (max-width:950px){

  main div.main-blog-con{
    grid-template-columns: auto auto;
  }

 

 

  div.movies-blog-content-con{
    grid-template-columns: 50% 50%;
  }
}

@media screen and (max-width:850px){

  div.about-main-con div.about-content-con{
    width: 100%;
  }
}

@media screen and (max-width:730px){

  main div.main-blog-con{
    grid-template-columns: auto;
  }

  nav div.main-nav-con div.nav-con{
    padding: 20px 40px;
  }

  nav div.main-nav-con div.nav-con div.menu ul{
    display: none;
  }

  div.nav-con div.menu button{
    display: inline-block;
   }

   div.phone-nav{
   display: block;
  }

  div.head-content{
    padding: 20px;
  }

  div.head-content h1{
    font-size: 2.5em;
    
  }
  
  div.head-content h2{
    font-size: 1.5em;
  
  }

  div.head-content form{
    width: 100%;
  }
}

@media screen and (max-width:600px){

  main div.main-con{
    /* border: 1px solid gray; */
    padding: 10px;
  }

 
}


@media screen and (max-width:580px){

  div.movies-blog-con > div:last-child h3{
    font-size: 1em;
  }
 
  div.head-content h1{
    font-size: 2em;
    
  }
  
  div.head-content h2{
    font-size: 1em;
  
  }

  div.about-main-con div.about-content-con{
    flex-direction: column;
  }

  div.contact-form-con{
    width: 70%;
    min-width: 300px;
  }

  div.contact-form-con div.contact-cir{
   margin-top: 50px;
  }
}

@keyframes manima{
  0%{
    transform: scale(1);
  }

  50%{
    transform: scale(0.9);
  }

  100%{
    transform: scale(1);
  }
}