div.main-search-blog-con {
  background-color: white;
  z-index: 10;
}

div.search-blog-con-popular {
  display: grid;
  grid-template-columns: 80% 20%;
  /* border: 2px solid pink; */
  align-items:start;
  background-color: white;
}

div.search-blog-con {
  /* border: 1px solid red; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 40px 0px;
}

div.search-blog-con h2 {
  font-size: 1.7em;
}

div.search-blog-con div.sub-search-blog-con {
  /* border: 1px solid khaki; */
  width: 400px;
}

div.search-blog-con div.sub-search-blog-con > div {
  margin: 20px 0px;
  background-color: rgb(245, 245, 245);
}

div.search-blog-con div.sub-search-blog-con div div.search-img-con {
  /* border: 1px solid gray; */
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

div.search-blog-con div.sub-search-blog-con div div.search-desc-con {
 padding: 0px 5px;
}

div.search-blog-con div.sub-search-blog-con div > :not(div img) {
  margin: 10px 0px;
}

div.search-blog-con div.sub-search-blog-con div img {
  width: 100%;
  /* height: 300px; 
  object-fit: cover; */
  transform: scale(1);
  transition: all 0.5s;
}

div.search-blog-con div.sub-search-blog-con > div:hover {
  background-color: rgb(219, 218, 218);
}

div.search-blog-con div.sub-search-blog-con div:hover img {
  transform: scale(1.2);
 }

div.search-blog-con div.sub-search-blog-con div a {
  text-decoration: none;
}

div.search-popular-post-con {
  /* border: 1px solid gray; */
  position: sticky;
  align-self: start;
  top: 0;
  background-color: white;
  padding: 20px 10px;
  /* overflow-y: scroll; */
}

div.search-popular-post-con div.search-input-con {
  display: flex;
}

div.search-popular-post-con div.search-input-con input {
  border: 1px solid rgba(167, 167, 167, 0.5);
  outline: none;
  padding: 10px 5px;
  width: 100%;
}

div.search-popular-post-con div.search-input-con a {
  text-decoration: none;
  color: black;
}

div.search-popular-post-con div.search-input-con span {
  background-color: tomato;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 5px;
}

div.search-popular-post-con div.popular-blog-search-con {
  /* border: 1px solid rgb(2, 18, 158); */
}

div.popular-blog-search-con h3 {
  padding: 10px 5px 0px 5px;
  text-align: center;
}

div.popular-blog-search-con div.subcon-popular-blog-search-con {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 10px 0px;
  box-sizing: border-box;
}

div.popular-blog-search-con div.subcon-popular-blog-search-con > div {
  margin: 10px 0px;
  cursor: pointer;
}

div.popular-blog-search-con div.subcon-popular-blog-search-con div img {
  width: 30%;
}

div.popular-blog-search-con div.subcon-popular-blog-search-con div h4 {
  font-size: 0.9em;
}

@media screen and (max-width: 600px) {
  div.search-blog-con-popular {
    grid-template-columns: 100%;
  }

  div.popular-blog-search-con div.subcon-popular-blog-search-con div img {
    width: 10%;
  }

}

@media screen and (max-width: 450px) {
    div.search-blog-con div.sub-search-blog-con {
        width: 100%;
      }

      div.search-blog-con {
        padding: 40px 20px;
      }
  }


