div.main-movie-blog-con{
    /* border : 2px solid tomato; */
}

div.movie-blog-con-popular{
    display: grid;
  grid-template-columns: 80% 20%;
  /* border: 2px solid pink; */
  align-items:start;
  background-color: white;
    
}

div.movie-blog-con-popular div.movie-blog-con{
    /* border: 1px solid black; */
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 20px 10px;
}

div.movie-blog-con-popular div.movie-blog-con > :not(no){
   margin: 10px 0px;
}

div.movie-blog-con div.sub-movie-blog-con{
    /* border: 3px solid red; */
    width: 800px;
    display: grid;
    grid-template-columns: 45% 45%;
    grid-gap: 20px;
    box-sizing: border-box;
    /* align-items: center; */
    justify-content: center;

}

div.sub-movie-blog-con div.movie-page-individual-blog-con{
    /* border: 2px solid lawngreen; */
    background-color: rgb(235, 235, 235);
}

div.sub-movie-blog-con div.movie-page-individual-blog-con:hover{
    /* border: 2px solid lawngreen; */
    background-color: rgb(218, 216, 216);
}

div.sub-movie-blog-con div.movie-page-individual-blog-con a{
   text-decoration: none;
}

div.movie-page-individual-blog-con div.movie-page-img-con {
   overflow: hidden;
   /* border: 1px solid black; */
   display: flex;
   justify-content: center;
   align-items: center;

}


div.movie-page-individual-blog-con div.movie-page-img-con img{
    width: 100%;
    /* object-fit: cover;
    height: 250px; */
    transition: all 0.5s;
}

div.sub-movie-blog-con div.movie-page-individual-blog-con:hover img{
   transform: scale(1.2);
}

div.sub-movie-blog-con div.movie-page-individual-blog-con div.movie-page-desc-con{
   padding: 10px 5px;
}

div.sub-movie-blog-con div.movie-page-individual-blog-con div.movie-page-desc-con p{
    color: rgb(240, 58, 231);
  }
 

div.sub-movie-blog-con div.movie-page-individual-blog-con div.movie-page-desc-con h3{
   font:bold;
 }

div.sub-movie-blog-con div.movie-page-individual-blog-con div.movie-page-desc-con :not(no){
    margin: 10px 0px;
 }

@media screen and (max-width:1050px){
    div.movie-blog-con div.sub-movie-blog-con{
       width: 100%;
    }
}

@media screen and (max-width:600px){
    div.movie-blog-con-popular{
      grid-template-columns: 100%;
    }

    div.sub-movie-blog-con div.movie-page-individual-blog-con h3{
        font-size: 15px;
    }
}